import { init, sendForm } from "@emailjs/browser";

init("init");

const form = document.getElementById("form");
const name = document.querySelector("#name");
const organization = document.querySelector("#organization");
const email = document.querySelector("#email");
const mobile = document.querySelector("#mobile");
const content = document.querySelector("#content");
const agree = document.querySelector("#agree");
const btn = document.getElementById("submit_button");

const checkMobileFormat = (e) => {
  const keyCode = e.keyCode;
  if (keyCode > 58 || keyCode < 47) e.preventDefault();
};

const sendEmail = (e) => {
  e.preventDefault();

  if (!agree.checked) {
    alert("Í∞úÏù∏Ï†ïÎ≥¥ ÏàòÏßë Ïù¥Ïö© ÏïΩÍ¥ÄÏóê ÎèôÏùòÌï¥Ï£ºÏÑ∏Ïöî.");
    return;
  }

  btn.value = "Sending...";

  const templateID = "templateID";
  const serviceID = "serviceID";

  sendForm(serviceID, templateID, "#form").then(
    () => {
      btn.value = "Î¨∏Ïùò ÎÇ¥Ïö© Ï†ÑÏÜ°ÌïòÍ∏∞";
      alert("Ïù¥Î©îÏùº Ï†ÑÏÜ°Ïù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.");
      name.value = "";
      organization.value = "";
      email.value = "";
      mobile.value = "";
      content.value = "";
      agree.checked = false;
    },
    (err) => {
      btn.value = "Î¨∏Ïùò ÎÇ¥Ïö© Ï†ÑÏÜ°ÌïòÍ∏∞";
      alert(JSON.stringify(err));
    }
  );
};

form.addEventListener("submit", sendEmail);
mobile.addEventListener("keypress", checkMobileFormat);
